<template>
    <div class="bei">
        <div class="head">
            <router-link tag="span" to="/">
            <i class="el-icon-arrow-left"></i>
            </router-link>
            <span>北京</span>
            <!-- {{this.city.name}} -->
            <span class="qie"> 
                <router-link tag="div" to="/ditu">切换城市</router-link>
                </span>
        </div>
        <div class="sou">
            <!-- <el-input v-model="input" placeholder="请输入学校、商务楼、地址" 
          ></el-input> -->
           <p> <input v-model="input" placeholder="请输入内容"  style="width:357px;height:39px;margin-Top:10px;background:white;border:1px solid gray;margin-left:25px;"></p>
           <!-- <p> <el-button type="primary" 
          ></el-button></p> -->
           <p><input type="button" round v-on:click="tijiao()" style="width:281px;marginLeft:52px;height:31px;lineHeight:0px;background:blue;textAline:center;color:white;border:none;" value='提交'></p>
        </div>
        <div class="li" style='marginTop:52px;'>
            <span>搜索历史</span>
            <div class="history">
                <!-- <div v-for="(item,key) in arr" :key="key"></div> -->
            </div>
        </div>
        <div class="yc">
            <router-link tag="span" to="/" class="item" v-for="(v,i) in list" :key="i">
               <span class="ming">{{v.name}}</span>
               <span class="dizhi">{{v.address}}</span>
            </router-link>
        </div>
        {{input}}
    </div>
</template>
<script>
// import rem from '../config/rem'
import axios from "axios"
// (function(doc, win) {
//     var docEl = doc.documentElement,
//         resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
//         recalc = function() {
//             var clientWidth = docEl.clientWidth;
//             if (!clientWidth) return;
//             docEl.style.fontSize = 20 * (clientWidth / 320) + 'px';
//         };
//     if (!doc.addEventListener) return;
//     win.addEventListener(resizeEvt, recalc, false);
//     doc.addEventListener('DOMContentLoaded', recalc, false);
// })(document, window);
export default {
    data(){
        return{
            input:"",
            city:[],
            list:[]
        }
    },
    mounted(){
        axios.get("http://elm.cangdu.org/v1/cities?type=guess").then(res=>{
            console.log(res.data)
            this.city=res.data
        })
    },
    methods:{
        tijiao(){
        if(this.input){
            axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.params.id+'&keyword='+this.input)
            .then(res=>{
                console.log(res.data)
                this.list=res.data
                })
            }
        }
    }
}
</script>
<style>
/* html,body,.bei,#app{
    height: 100%;
    margin: 0;
    padding: 0;
    position: relative;
} */
.bei{
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F4F4;
    position: relative;
    background: #F4F4F4;
}
.head{
    width: 100%;
    height: 2rem;
    background: #3792E5;
    display: flex;
    justify-content: space-between;
}
.head>i{
    color: white;
    line-height: 1rem;
    font-size: 18px;
    display: block;
}
.head>span{
    color: white;
    font-size: 1rem;
    line-height: 2rem;
}
.head>.qie{
    color: white;
    font-size: 0.7rem;
    line-height: 2rem;
}
.sou{
    width: 100%;
    height: 5rem;
    background: white;
    margin-top: 0.8rem;
}
.li{
    width:100%;
    height: 1.3rem;
    border-bottom: 1px solid #cccccc;
}
.li>span{
    line-height: 1.3rem;
    margin-left: 0.2rem;
    font-size: 0.3rem;
    color: gray;
}
.yc{
    width: 100%;
    background: white;
}
.item{
    width: 100%;
    height: 4rem;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid gray;
}
.ming{
    line-height: 2rem;
    font-size: 0.6rem;
    margin-left: 0.5rem;
}
.dizhi{
    line-height: 1rem;
    font-size: 0.4rem;
    margin-left: 0.5rem;
    color: gray;
}
</style>


