<template>
<div>
    <div class="hea">
     <router-link tag='span' to='/'>
        <img src="@/images/110.png" alt="">
    </router-link>
     <div class="not">订单信息</div>
    </div>
    <div class="content">
        <div class="left">
            <ul>
                <li class="li1" v-for='(v,i) in list' :key="i">{{v.name}}</li>
                <!-- <li class="li2"><a href="'fa'+i">{{this.data.name}}</a></li> -->
            </ul>
        </div>
        <div class="right" ref="right">
            <div class="two" v-for="(v,i) in list" :key='i'>
                <h2 :id="'fa'+i"></h2><!-- {{v.name}} -->
                <div class="zuo" ></div><!-- v-for="(item,k) in v.foods" :key="k"-->
                <div class="tu">
                    <img :src="'http://elm.cangdu.org/img/'+v.image_path" alt="">
                </div>
                <div class="wenzi">
                    <span class="span1"></span><!-- {{item.name}}-->
                    <span class="span2"></span><!-- {{item.description}}-->
                    <span class="span3"></span><!-- {{item.tips}}-->
                    <span class="span4">的</span>
                    <span class="span5">￥<span>20</span><span class="q1">起</span></span>
                </div>
                <div class="you">
                    <div class="zhao">
                        <span>招牌</span>
                    </div>
                    <div class="jia" @click="yundong($event,k)">+</div>
                </div>
            </div>
        </div>
    </div>

    </div>
</template>

<script>
import { Sidebar, SidebarItem } from 'vant';
import axios from 'axios'
import jq from 'jquery'
export default {
  data() {
    return {
      activeKey: 0,
      list:[]
    };
  },
  mounted(){
      axios.get('http://elm.cangdu.org/shopping/v2/menu?restaurant_id=1').then((res)=>{
          console.log(res.data)
          this.list=res.data
      })
  }

};
</script>

<style lang="scss" scoped >
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.content{
    width: 100%;
    // display: flex;
    position: relative;
    .left{
        width: 27%;
        height: 33rem;
        position:absolute;
        overflow: auto;
        ul li{
            list-style: none;
            margin: 0;
            padding: 0;
        }
        ul{
            width: 100%;
            display:flex;
            height: 2.5rem;
            border-bottom: #cccccc 1px solid;
            background: #f5f5f5;
            display: flex;
            flex-direction: column;
            .li1{
                line-height: 2.5rem;
                margin-left:0.5rem;
                font-size: 0.6rem;
                a{
                   color: black;
                }
            }
        }
        
    }
    .li2{
        width: 0.3rem;
        height: 2.5rem;
    }
}
ul:hover{
    background: white;
    .li2{
        background: #3190E8;
        z-index: 500;
    }
}
.left:hover{
    background: white;
    .zuo:hover{
        background: #3190E8;
    }
}
.right{
    width: 73%;
    height: 33rem;
    position: absolute;
    left: 27%;
    overflow: auto;
    border-bottom: 1px solid #f5f5f5;
    .one{
        width: 100%;
        height: 2rem;
        display: flex;
        background: #f5f5f5;
        justify-content: #cccccc 1px solid;
        justify-content: space-between;
        overflow: hidden;
        font-size: 0.7rem;
        .zi{
            display: flex;
            p{
                line-height: 0.4rem;
                font-size: 0.8rem;
                margin-left: 10px;
            }
            span{
                line-height: 2rem;
                margin-left: 10px;
                color: gray;
            }
        }
        .dian{
            width: 1rem;
                span{
                    line-height: 2rem;
                    color: gray;
                }
        }
        
    }
    .two{
        width: 100%;
        height: auto;
        border-bottom: #cccccc 1px solid;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        h3{
            width: 100%;
        }
        .zuo{
            display: flex; 
            border-bottom: 1px solid #cccccc; 
            justify-content: space-between;
        .tu{
            width: 2.3rem;
            height: 2.2rem;
            border: 1px solid #cccccc;
            margin-left: 0.3rem;
            margin-top: 0.3rem;
        img{
            width: 2.3rem;
            height: 2.2rem;
        }
        }
        .wenzi{
            display: flex;
            flex-direction: column;
            margin-left: 0.4rem;
        .span1{
            margin-top: 0.3rem;
            font-size: 0.7rem;
            font-weight: bold;
        }
        .span2{
            color: gray;
            margin-top: 0.1rem;
        }
        .span3{
            margin-top: 0.1rem;
        span{
            margin-left: 0.3rem;
        }
        }
        .span4{
            // margin-top: 0.1rem;
            width: 0.8rem;
            height: 0.8rem;
            border: 1px solid orangered;
            text-align: center;
            line-height: 0.8rem;
            border-radius: 50%;
            color: orangered;
        }
        .span5{
            margin-top: 0.2rem;
            color: orangered;
            font-size: 0.5rem;
        span{
            font-size: 0.7rem;
            font-weight: bold;
        }
        .qi{
            color: gray;
            margin-left: 0.2rem;
            font-size: 0.4rem;
        }
        }
        }
        }
        }
        .you{
            display: flex;
            flex-direction: column;
            .zhao{
            width: 1.5rem;
            height: 0.6rem;
            margin-top: 0.5rem;
            border: orangered 1px solid; 
            border-radius: 0.3rem;
            margin-right: 0.3rem;
            text-align: center;
            span{
            color: orangered;
            line-height: 0.6rem;
            font-size: 0.3rem;
            display: block;
            }
            }
            .xuan{
            width: 2rem;
            height: 1.3rem;
            background: #3190E8;
            margin-top: 1.8rem;
            // margin-right: 0.4rem;
            display: block;
            border-radius: 0.3rem;
            text-align: center;
            span{
            line-height: 1.3rem;
            color: white;
            font-size: 0.5rem;
            }
            }
            .jia{
            width: 1rem;
            height: 1rem;
            border-radius: 50%;
            background: #3190E8;
            color: white;
            text-align: center;
            line-height: 1rem;
            margin-top: 1.2rem;
            }
            }
}
</style>


