import Vue from 'vue'
import Router from 'vue-router'


import Ditu from '@/page/Ditu'
import Home from '@/page/Home'
// import Swiper from '@/page/Swiper'
import Childone from '@/page/Childone'

import Cartcontrol from '@/pages/Cartcontrol'

import Cart from '@/pages/cart'
import City from '@/page/city/City'
import Addreess from '@/page/city/Addreess'
import Use from '@/page/city/Use'
import Copliy from '@/page/city/Copliy'
import Pict from '@/page/city/Pict'
import AddDetail from '@/page/city/AddDetail'
import Search from '@/pages/Search'
import Login from '@/pages/Login'
import Mine from '@/pages/Mine'
import SouS from '@/pages/SouS'
import Order from '@/pages/Order'
import Shop from '@/mine/Shop'

import { Sidebar, SidebarItem } from 'vant';
Vue.use(Router)
Vue.use(Sidebar);
Vue.use(SidebarItem);
export default new Router({
  routes: [
    {
      path:"/",
      name:"Home",
      component:Home,
      children:[
        
      ]
    },
    {
      path: '/Order',
      name: 'Order',
      component: Order
     }, 
     {
      path: '/Cart',
      name: 'Cart',
      component: Cart
     },
     {
      path: '/Cartcontrol',
      name: 'Cartcontrol',
      component: Cartcontrol
     },
    {
      path: '/SouS',
      name: 'SouS',
      component: SouS
    },
    
    {
      path: '/City',
      name: 'City',
      component: City,
    },
    {
      path: '/Pict',
      name: 'Pict',
      component: Pict,
    },
    {
      path: '/Addreess',
      name: 'Addreess',
      component: Addreess,
    },
    {
      path: '/Use',
      name: 'Use',
      component: Use,
    },
    {
      path: '/copliy',
      name: 'Copliy',
      component: Copliy,
    },
    {
      path: '/Ditu',
      name: 'Ditu',
      component: Ditu,
    },
    {
      path: '/Childone',
      name: 'Childone',
      component: Childone
    },
    // {
    //   path: '/Swiper',
    //   name: 'Swiper',
    //   component: Swiper
    // },
    {
      path: '/Mine',
      name: 'Mine',
      component: Mine
    },
    {
      path:'/AddDetail',
      name:AddDetail,
      component:AddDetail
    },

    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path:'/Search',
      name:'Search',
      component:Search
    },
    {
      path: '/Shop',
      name: 'Shop',
      component: Shop,
    },
  ]
})



