<template>
  <div class="body">
  
  
    <div class="xiang">
      <div class="xiang_a">
        <div class="xiang_aa"
             v-for="(item,i) in list"
             :key="i">
          <div @click="dianwo(i)">{{item.name}}</div>
        </div>
      </div>
      <div class="xiang_b">
        <div v-for="(item,i) in lis"
             :key="i"
             class="xiang_bc">
          <div class="xiang_ba">
            <img :src="'//elm.cangdu.org/img/'+item.image_path"
                 alt="">
          </div>
          <div class="xiang_bb">
            <div class="xiang_bd">{{item.name}}</div>
            <div class="xiang_be">{{item.description}}</div>
            <div class="xiang_bf">
              <span class="xiang_bg">月售{{item.month_sales}}份 </span>
              <span>好评{{item.satisfy_rate}}%</span>
            </div>
            <div class="xiang_bh"
                 v-if='item.activity'><span>{{item.activity.image_text}}</span></div>
            <div class="xiang_bh"
                 v-else></div>
            <div class="xiang_bi">￥{{item.specfoods[0].price}}</div>
            <button v-if="item.specifications.name">选规格</button>
            <button v-else='' @click="gouwu(i,$event)" class="jia">+</button>
          </div>
        </div>
      </div>
        <van-popup v-model="show" position='buttom' id="aaaa">内容
              
          <span @click="del">x</span>
        </van-popup>
      <div class="addshop">
        <!-- <router-link to="/Goshop"> -->
          
          <van-button type="primary" @click="showPopup">
            <div class="car sc">
              <img src="@/images/22.png" alt="">
            </div>
          </van-button>
          
        <!-- </router-link> -->
          <div class="money">
            <p>￥0.0元</p>
            <p>配送费￥5元</p>
          </div>
          <div class="goaway">
            还差20元起送
          </div>
      </div>
    
      
    </div>
    
  </div>
</template>
<script>
import { Icon } from "vant";
import axios from "axios";
import '../../src/theball/theball/theBall.css'
import the from '../../src/theball/theball/theBall.js'
import Vue from 'vue';
import { Popup } from 'vant';

Vue.use(Popup);
export default {
  data() {
    return {
      list: [],
      index: 0,
      lis: [],
      show: false
    };
  },
  // components:{
  //   TheBall
  // },
  methods: {
    del(){
       this.show = false;
    },
     showPopup() {
      this.show = true;
    },
    dianwo(i) {
      // this.index = i
      axios.get("../static/data.json").then(res => {
        this.lis = res.data[i].foods;
        console.log(res.data[i].foods);
      });
      // console.log(this.index)
    },
    gouwu(i,e) {
      the(e)
      let tt = this.lis[i];
      console.log(tt);
      this.$store.commit("gouwu", tt);
    }
  },
  mounted() {
    axios.get("../static/data.json").then(res => {
      // console.log(res.data)
      this.list = res.data;
      this.lis = res.data[this.index].foods;
      console.log(this.lis);
    });
  }
};
</script>
<style lang="scss" scoped >
.body{
  width: 100%;
  height: 100%;
}
#aaaa{
  width: 60%;
  height: 40%;
  background: white;
  position: fixed;
  top: 5rem;
  left: 4rem;
}
.hea{
    position: fixed;
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 01rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.xiang-header {
  width: 100%;
  height: 4rem;
}
.addshop {
  width: 100%;
  height: 4rem;
  position: fixed;
  bottom: 0;
  left: 0;
  background: #3d3d3f;
  // z-index: 100;
  color: white;
}
.car{
    width: 5rem;
    height: 5rem;
    background: #3d3d3f;
    border: 1px solid gray;
    border-radius: 50%;
    position: absolute;
    top:-30.5px;
    left: 10px;
    float: left;
    img{
      position: absolute;
      left:15px;
      top: 15px;
    }
  }
  .money{
    float: left;
    margin-left:115px; 
    margin-top: -10px;
  }
  .goaway{
    float:right;
    margin-top: 1.5rem;
    margin-right: 1.5rem;
  }
.xiang {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  position:relative;
}
.xiang_a {
  width: 25%;
  // height: 100%;
  // overflow: hidden;
  font-size: 0.7rem;
}
.xiang_aa {
  border-bottom: 0.01rem solid #ccc;
  padding: 5%;
  background: #f2f2f2;
  height:5rem;
  line-height: 5rem;
  text-align: center;
}
.xiang_aa:hover {
  background: white;
}
.xiang_b {
  width: 70%;
  font-size: 0.7rem;
}
.xiang_ba {
  width: 30%;
}
.xiang_ba img {
  width: 70%;
}
.xiang_bc {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  border-bottom: 0.02rem solid #ccc;
  padding: 3%;
}
.xiang_bb {
  width: 70%;
  text-align: left;
}
.xiang_be {
  font-size: 0.4rem;
}
.xiang_bf {
  font-size: 0.5rem;
}
.xiang_bh span {
  font-size: 0.4rem;
  border: 0.03rem solid #f1884f;
  border-radius: 0.2rem;
}
.xiang_bi {
  font-size: 0.6rem;
  color: #f1884f;
}
.jia {
  background: #308de4;
  border-radius: 15px;
  height: 1rem;
  width: 6%;
  color: white;
  position: absolute;
  right: 1rem;
  margin-top: -1rem;
  border:none;
}
</style>


