<template>
<div>
      <!-- 头 -->
 <div class="hea">
     <router-link tag='span' to='/Mine'>
        <img src="@/images/110.png" alt="">
    </router-link>
     
      <div class="not">账户信息</div>
    </div>
    <!-- 内容 -->
    <div class="shop-con">
        <div class="shop-tou" @click=faB()>
            <span>头像</span>
            <!-- <div class="shop-xiang"> -->
                    <img :src="img1" alt="" class="img1">
                    <input type="file" accept="imgp/*" @change="faA($event)" ref="inp" alt class="inp"/>
                <div>></div>
            <!-- </div> -->
        </div>
      
            <div class="shop-yong">
                <p>用户名</p>
                 <router-link to='/Use'> <p>></p></router-link>
            </div>
        
        
        <div class="shop-shou">
            <p>收货地址</p>
              <router-link to='/Addreess'> <p>></p></router-link>
        </div>
        <div style="font-size:0.8rem;">
            账号绑定
        </div>
        <div class="shop-shou">
            <p>📱手机</p>
            <p>></p>
              <!-- <router-link to='/Phone'> </router-link> -->
        </div>
        <div style="font-size:0.8rem;">
            安全设置
        </div>
        <div class="shop-shou">
            <p>登陆密码</p>
              <router-link to='/Copliy'> <p>修改</p></router-link>
        </div>
        <div class="shop-chu">
            <input type="button" value="退出登录" class="shop-tui" >
        </div>
        
    </div>
  </div>
</template>


<script>
import Vue from 'vue'
    import axios from "axios";
    export default {
        components:{
        },
        name: "Shop",
        data() {
            return{
                 img1:'http://elm.cangdu.org/img/default.jpg'
            }
        },
        methods:{
            faA(e){
               var ev=e.target;
               
               var x=ev.files[0]
                    console.log(x)
                    var red=new FileReader()
                    red.onload=(data)=>{
                        var s=data.target;
                        this.img1=s.result
                        console.log(this.img1)
                        localStorage.img1=this.img1
                    }
                    red.readAsDataURL(x)
            },
            faB(){
                this.$el.querySelector('.inp').click()
            }
        }
    }
</script>


<style scoped lang='scss'>
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.shop-con{
    width: 100%;
}
.shop-tou{
    width: 100%;
    height: 4rem;
    background: white;
    margin-top: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid darkgray;
    span{
        display:block;
        width: 2rem;
        // border: 1px solid saddlebrown;
    }
    .img1{
        display: flex;
        align-items: center;
        width: 2.8rem;
        height: 2.8rem;
        border-radius: 50%;
        margin-left:18rem;
        // border: 1px solid saddlebrown;
    }
    .inp{
        display: none;
    }
}
// .shop-xiang{
//     display: flex;
//     align-items: center;
// }
// .shop-xiang img{
//     width: 3rem;
//     height: 3rem;
//     border-radius: 50%;
// }
.tupian{
    display: block;
    position: fixed;
    opacity: 0;
    top: 3rem;
    left: 15rem;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    border: 1px solid red;
}
.tupian img{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
}
.shop-yong{
    width: 100%;
    height: 3rem;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid darkgray;
}
.shop-shou{
    width: 100%;
    height: 3rem;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid darkgray;
}
.shop-chu{
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
}
.shop-tui{
    width: 95%;
    height: 2.5rem;
    background: #d8584a;
    border-radius: 0.5rem;
    border: none;
    color: white;
}
</style>


