<template>
<div>
    <div class="hea">
     <router-link tag='span' to='/'>
        <img src="@/images/110.png" alt="">
    </router-link>
     <div class="not">我的</div>
    </div>
    <router-link to="/shop" tag="div">
    <div class="my-head">
        <div>
            <img class="img" :src="img1" alt/>
        </div>
        <div>
            <!-- <p>{{this.Arrlist}}</p> -->
            <p>{{txt}}📱</p>
        </div>
        <div>
            >
        </div>
    </div>
    </router-link>
    <div class="my-con">
        <div>
            <p><span class="my-con-cor">0.00</span>元</p>
            <p>我的余额</p>
        </div>
        <div>
            <p><span class="my-con-col">3</span>个</p>
            <p>我的优惠</p>
        </div>
        <div>
            <p><span class="my-con-colo">0</span>分</p>
            <p>我的积分</p>
        </div>
    </div>
    <div class="my-down">
        <router-link to="/oreder" tag="div"><p><i class="el-icon-s-unfold"></i>我的订单</p></router-link>
        <p>></p>
    </div>
    <div class="my-dow">
        <router-link to="/oreder" tag="div"><p><i class="el-icon-printer"></i>积分商城</p></router-link>
        <p>></p>
    </div>
    <div class="my-dow">
        <router-link to="/oreder" tag="div"><p><i class="el-icon-star-on"></i>饿了么会员卡</p></router-link>
        <p>></p>
    </div>
    <div class="my-down">
        <router-link to="/oreder" tag="div"><p><i class="el-icon-news"></i>服务中心</p></router-link>
        
        <p>></p>
    </div>
    <div class="my-dow">
        <router-link to="/oreder" tag="div"><p><i class="el-icon-sold-out"></i>下载饿了么App</p></router-link>
        <p>></p>
    </div>
</div>
  
</template>
<script>
import axios from 'axios'
import { log } from 'util';
export default {
    components:{
    },
    data(){
        return{
            txt:'',
            Arr:'',
            Arrlist:[],
            img1:'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1846948884,880298315&fm=26&gp=0.jpg'
        }
        
    },
    mounted(){
        this.txt=localStorage.手机号
        this.Arr=JSON.parse(localStorage.getItem('key'));
        console.log(this.Arr)
         axios.get("http://elm.cangdu.org/v1/user?user_id="+this.Arr).then((res) => {
            console.log(res)
            this.Arrlist=res.data.username
    //   console.log(JSON.parse(localStorage.getItem("id")))
        let obj=JSON.parse(localStorage.getItem("id"))
        console.log(obj[0])
        this.txt=obj[0].name
        
   })
   if(localStorage.img1){
       this.img1=localStorage.img1
   }
    }
}
</script>
<style lang='scss' scoped>
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.my-head{
    width: 100%;
    height: 5rem;
    background: #3792E5;
    display: flex;
    align-items: center;
    color: rgb(255, 255, 255);
    justify-content: space-between;
}
.my-head div:nth-of-type(1){
    width: 4rem;
    height: 4rem;
    /* border-radius: 5rem; */
    /* border: 1px solid black; */
    line-height: 3rem;
}
.my-con{
    width: 100%;
    height: 5rem;
    background: white;
    display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.my-con div{
    width: 33.33%;
    height: 100%;
    border-right: 1px solid darkgray;
    text-align: center;
    /* line-height: 2rem; */
}
.my-con-cor{
    font-size: 1.5rem;
    color: rgb(231, 247, 6);
}
.my-con-col{
    font-size: 1.5rem;
    color: red;
}
.my-con-colo{
    font-size: 1.5rem;
    color: green;
}
.my-head div img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.my-down{
    color: darkgray;
    width: 100%;
    height: 3rem;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.5rem;
    border-bottom: 1px solid rgb(224, 218, 218);
}
.my-dow{
    color: darkgray;


    width: 100%;
    height: 3rem;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>


