<template>
<div>
    <div class="hea">
     <router-link tag='span' to='/Addreess'>
        <img src="@/images/110.png" alt=""><div class="not">新增地址</div>
    </router-link>
     <div class="address">
         <input type="text" placeholder="请填写你的姓名" v-model='txt2' >
         <p></p>
         <!-- <router-link tag='p' to='/SouS'> -->
         <input type="text" placeholder="小区/写字楼/学校等"  v-model="txt1">
         <!-- </router-link> -->
         <input type="text" placeholder="请填写详细送餐地址" class="thC" v-model='txt3'>
         <p></p>
         <input type="text" placeholder="请填写能够联系到你的手机号" class="thD" v-model='txt4'>
         <p></p>
         <input type="text" placeholder="备用联系电话（选填）" class="thE" v-model='txt5'>
         <p></p>
         <button @click="faA()">新增地址</button>
     </div>
    </div>
    <!-- <router-link to="/shop" tag="div"></router-link> -->
</div>
  
</template>
<script>
import axios from 'axios'
export default {
    components:{
    },
    data(){
        return{
            txt1:'',
            txt2:'',
            txt3:'',
            txt4:'',
            txt5:'',

        }
        
    },
    methods:{
         faA(){
           axios
.post("http://elm.cangdu.org/v1/users/38453/addresses", {
        address:this.txt3,
        address_detail: this.txt1,
        geohash: "31.22299,121.36025",
        name: this.txt2,
        phone: this.txt4,
        phone_bk: this.txt5,
        poi_type: 0,
        sex: 1,
        tag: "公司",
        tag_type: 4
})
.then(res => {
console.log(res)    
});
}
},
    mounted(){
        if(this.$route.query.name){
            console.log(this.$route.query.name)
          this.txt1=this.$route.query.name;
          console.log(this.txt1)
        }
        
    },
    // beforeMount

}
</script>


<style lang='scss' scoped>
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.address{
    input{
        width: 92%;
        height: 2rem;
        margin-top:0.34rem;
        margin-left: 1rem;
        border-radius: 0.15rem;
        border: none;
    }
    button{
        width:90%;
        height: 2.2rem;
        margin-left: 1.2rem;
        border: none;
        background: #4cd964;
        border-radius: 0.15rem;
    }
}
</style>


