<template>
  <div>
    <!-- <mt-header :title="$store.state.nowcity.name" class="fs1-2" fixed>
      <mt-button slot="left">
        <mt-button icon="back"></mt-button>
      </mt-button>
      <mt-button slot="right" class="fs0-8">切换城市</mt-button>
    </mt-header> -->
    
    <indexHeader>
      <div slot="left">
        <router-link to='/'>
          <img src="@/images/110.png" alt="" style="width:1.5rem;height:1.5rem;">
        </router-link>
      </div>
      <div slot="cityHeader">
          <router-link tag='p' to='/ditu'>
            {{this.$route.query.add}}
          </router-link>
      </div>  
    </indexHeader>
    <div class="mgtop50 padlr10 bgfff padbot10" style="margin-top:50px;">
      <input class="cityinput" v-model="txt" type="text" placeholder="输入商务楼，学校，地址" />
      <div class="submit bgcol ih40" @click="onSearch()">提交</div>
    </div>

    <ul>
        <router-link :to="'/?name='+item.name" class="li1" v-for="(item,key) in arr" :key="key">
          {{item.name}}<br />
          {{item.address}}
        </router-link>
    </ul>
  </div>
</template>

<script>
import indexHeader from '@/components/index-header.vue'
import axios from 'axios'
export default {
  data() {
    return {
      inputval: "",
      list: "",
      txt:'',
      arr:[]
    };
  },
  components: {
    //注册组件
    indexHeader
  },
  mounted: function() {
    //生命周期
  },
  computed: {
    //计算属性
  },
  methods: {
    //函数
    onSearch(){
      axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.query.id+'&keyword='+this.txt).then((res)=>{
          this.arr=res.data
          console.log(this.arr)
      })
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

.cityinput {
  width: 100%;
  height: 40px;
  margin: 10px 0px;
  outline: 0px;
  padding: 0px 5px;
  box-sizing: border-box;
}
.submit {
  text-align: center;
  color: white;
  border-radius: 3px;
}
.fs0-8 {
  font-size: 0.8rem !important;
}
.li1{
  width: 100%;
  height: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main {
  border-top: 2px solid #e4e4e4;
}
.maintop {
  border-bottom: 2px solid #e4e4e4;
}
.clearall {
  text-align: center;
}
</style>


