<template>
<div>
    <div class="hea">
     <router-link tag='span' to='/Shop'>
        <img src="@/images/110.png" alt="">
    </router-link>
      <div class="not">编辑地址</div>
      <router-link tag='span' to='/'>
        <div class="we">编辑</div>
    </router-link>
    </div>
    <router-link to="/Pict" tag="div">
    <div class="adres">
        <span>新增地址</span>
        <span><img src="@/images/2.png" alt=""></span>
    </div>
    </router-link>
</div>
  
</template>
<script>
import axios from 'axios'
export default {
    components:{
    },
    data(){
        return{
            list:[],
            txt:'',
            Arr:'',
            Arrlist:[],
            // img1:'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1846948884,880298315&fm=26&gp=0.jpg'
        }
        
    },
    mounted(){
         axios.get(`http://elm.cangdu.org/v1/users/38453/addresses`).then((e)=>{
        console.log(e)
        this.list=e.data
        
        
    })
        this.txt=localStorage.用户名
        this.Arr=JSON.parse(localStorage.getItem('key'));
        console.log(this.Arr)
//          axios.get("http://elm.cangdu.org/v1/user?user_id="+this.Arr).then((res) => {
//             console.log(res)
//             this.Arrlist=res.data.username
//    })
   if(localStorage.img1){
       this.img1=localStorage.img1
   }
    }
   

}
</script>


<style lang="scss">
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 6.6rem;
        line-height: 3rem;
    }
    .we{
        line-height: 3rem;
        display: inline-block;
        margin-left:7.9rem;
    }
}
.adres{
    position: relative;
    width: 99%;
    height: 2.8rem;
    // line-height: 2.8rem;
    border:1px solid grey;
    background: #fff;
    margin-top: 1rem;
    span:nth-of-type(1){
        float: left;
        margin-top: inherit;
        margin-left:1rem;
    }
    span:nth-of-type(2){
        margin-left: 18rem;
        img{
            position: absolute;
            margin-top: 0.3rem;
            top:0.2rem;
            left: 22rem;
        }
    }
}
</style>


