<template>
  <div>
    <header>
        <slot name="left"><router-link to='/city'><img src="@/images/110.png" alt=""></router-link></slot>
        <slot name="cityHeader"><router-link tag='p' to='/ditu'>密码登录</router-link></slot>
    </header>
      <div class="content">
        <input type="text" placeholder="账号" v-model="username">
        <input :type="typeX" placeholder="密码" v-model="password">
        <!-- <div class="look"> -->
          <input type="text" placeholder="验证码" v-model="captcha_code">
        <div class="button_switch" style="color:grey;">
          <p>
            <input type="range" id="range" min="1" step="1" max="2" @change="onOff()" />
          </p>
        </div>
        <div class="look">
          <!-- <input type="text" placeholder="验证码" v-model="captcha_code"> -->
          <img v-if="code" :src="code" alt="">
          <p @click="changeImg"> <span>看不清</span> 换一张</p>
        </div>
        <div class="tishi" style="color:red;font-size:0.14rem;margin-top:0.4rem;">
          <p style="margin-bottom:1rem;margin-left:1.2rem;">温馨提示未注册过的账号，登录时将自动注册</p>
          <p style="margin-left:1.2rem;">注册过的用户可凭账号密码登录</p>
      </div>
        
      </div>
      
      <div class="Verification Code">
          <!-- <input type="button" value="登录" @click="login"> -->
          <button @click="login" style="width:24rem;height:3.4rem;margin-left:1rem;color:white;background:#4cd964;border:none;border-radius:0.5rem;">登录</button>
        </div>
<!--     
    <div class="daohang"><router-link to="/childone"><img src="@/images/110.png" alt=""></router-link></div>
    <div class="login-wrap" v-show="showLogin">
      <h3 style="color:white;font-size:16px;text-aline:center;margin-top:-20px;">密码登录</h3>
      <p v-show="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入用户名" v-model="username">
      <input type="password" placeholder="请输入密码" v-model="password">
      <button v-on:click="login">登录</button>
    <indexHeader></indexHeader>
    </div>

    <div class="register-wrap" v-show="showRegister">
      <h3>注册</h3>
      <p v-show="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入用户名" v-model="newUsername">
      <input type="password" placeholder="请输入密码" v-model="newPassword">
      <button v-on:click="register">注册</button>
      <span v-on:click="ToLogin">已有账号？马上登录</span>
    </div> -->
  </div>
</template>
<script>
import axios from 'axios';
import { log } from 'util';
// 让axios携带cookie
axios.defaults.withCredentials=true;
// import indexHeader from '@/components/index-header.vue'
export default {
  // name: "#login",
  data(){
    return{
      code:'',
      captcha_code:'',
      username:'',
      password:'',
      off:false,
      typeX:'password',
    }
  },
  mounted(){
    // 控制密码可视
    this.shi=!this.shi 
    if(this.shi==true){
      this.type='txt'
    }else{
      this.type='password'
    }
    this.changeImg();

  },
  methods:{
    // 验证登陆
    login(){
      axios.post('http://elm.cangdu.org/v2/login',{
        
        captcha_code:this.captcha_code,
        password:this.password,
        username:this.username
    }).then((res)=>{
      let obj=[{
        id:res.data.user_id,
        name:res.data.username
      }]
      console.log(res)
      // localStorage.手机号=this.username
        localStorage.setItem("id",JSON.stringify(obj))//JSON.stringify(obj)吧obj转化为字符串
      // this.$router.go(-1)
    })
    },
    changeImg(){
      // 验证码
      axios.post('http://elm.cangdu.org/v1/captchas',{})
      .then((res)=>{
        if(res.data.code!=''){
          this.code=res.data.code;
        }
      })
    },
    // 密码是否可视
    onOff(){
      this.off=!this.off
      if (this.off==true) {
        this.typeX='text';
        var range = document.getElementById('range').style.background='#4cd964'
        // console.log(range)
      }else{
        this.typeX='password';
        var range = document.getElementById('range').style.background='#cccccc'
        // console.log(range)
      }
      // console.log(this.off)
    },
    go(){
      this.$router.go(-1);
    }
  }
}
</script>

<style lang="scss" scoped>

*{
  margin: 0;
  padding: 0;
}
header{
    width:100%;
    height:3.2rem;
    display: flex;
    justify-content: left;
    align-items: center;
    position: relative;
    color: white;
    p{
      margin-left:10rem;
    }
    img {
    width: 1.6rem;
    height: 1.8rem;
  }
}
.content{
  width: 100%;
  height: 100%;
  margin-top: 1rem;
  input{
    width: 100%;
    height:3rem;
    border:none;
  }
  input[type=range]{
    -webkit-appearance: none;
    width: 2.4rem;
    height: 1.2rem;
    border-radius: 0.4rem;
    position: relative;
    top:-4.2rem;
    background: #cccccc;
  }
  input[type=range]::-webkit-slider-thumb{
      -webkit-appearance: none;
      width: 1.2rem;
      height: 1.4rem;
      border-radius: 50%;
      
      background: #f1f1f1;
  }
}
.button_switch{
  position: relative;
  top: -2.1rem;
  left: 20rem; 
}
.look{
  width: 100%;
  height:0rem;
  img{
    position: relative;
    top: -3.5rem;
    left: 15rem;
  }
  p{
    position: relative;
    top: -6rem;
    left:20rem;
    span{
      display: block;
    }
  }
}

</style>


