<template>
  <div class="home">
    <!-- <div class="hea">
      <div class="not1">
        <router-link to="/ditu">
          <img src="../images/1.png" alt />
        </router-link>
      </div>
       <div class="not2"><router-link to='/hello'>普陀区阳光威尼斯C...</router-link></div> 
      <div class="not2">普陀区阳光威尼斯C...</div>
      <div class="not3">
        <router-link to="/Login">
          <img src="../images/120.png" alt="">
        </router-link>
      </div>
    </div> -->
   
    <indexHeader>
      <div slot="cityHeader">
        <router-link tag='p' v-show="this.$route.query.name==undefined" to='/ditu'>
            请选择城市...
        </router-link>
         <router-link tag='p' v-show="this.$route.query.name!=undefined" to='/ditu'>
         <!-- 接受login传递过来的参数 -->
            {{this.$route.query.name}}
        </router-link>
      </div>
    </indexHeader>
     <!-- 轮播图渲染部分 -->
      <!-- <Swiper></Swiper>   -->
    <div id="con">
   <div class="swiper-container">
      <div class="swiper-wrapper">
         <div class="swiper-slide" v-for="(item,index) in lunBo" :key="index">
            <div class="slider" v-for="foodTu in item" :key="foodTu.id">
               <div><img :src="'https://fuss10.elemecdn.com'+foodTu.image_url" alt=""></div>
               <div>{{foodTu.title}}</div>
            </div>
         </div>
      </div>
      <div class="swiper-pagination"></div>
   </div>
</div>
    <p style="width:100%;height:10px;background:#f6f6f6;"></p>
    <!-- 附近商家 -->
    <div class="house">
      <!-- <p class="pic1"><img src="../images/11.png" alt=""><span>附近商家</span></p> -->
       <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
      <div class="testdiv">
        <div class="testimg">
          <img src="../images/11.png" alt />
        </div>
        <div class="testtext">
          <span>附近商家</span>
        </div>
      </div>
      <div class="shop">
        <div class="shopl1"  v-for="(i,index) in dataList" :key='index' @click="add">
          <div class="left">
            <img :src="'//elm.cangdu.org/img/'+i.image_path" alt/>
          </div>
          <div class="right">
            <p class="q1" >
              <span
                style="font-size:13px;background-color:#ffd94f;border-redius:8px;font-weight:bold;"
              >品牌</span>
              <span style="font-weight:bold;margin-left:5px;font-size:14px;">{{i.name}}</span>
              <span style="color:gray; font-size:10px;border:1px solid gray;margin-left:60x;">{{i.rating}}分</span>
            </p>
            <p class="q2">
              <span style="color:#f19534;font-size:7px;">4.7</span>
              <span style="font-size:14px;color:gray; font-size:13px;">⭐⭐⭐⭐⭐月售{{i.recent_order_num}}单</span>
              <span style="font-size:8px;background-color:#4088e0;color:white;">蜂鸟专送</span>
              <span style="color:#4088e0;border:1px solid #4088e0;font-size:8px;">准时达</span>
            </p>
            <p class="q3">
              <span style="font-size:9px;">￥{{i.float_minimun_order_amount}}起送/配送费约￥{{i.float_delivery_fee}}</span>
              <span style="color:gray; font-size:9px;">1386.9公里/</span>
              <span style="color:#4088e0;font-size:9px;">14小时47分钟</span>
            </p>
          </div>
        </div>
      </div>
      </mescroll-vue>
    </div>
    <div>
      <div id="footers">
        <div class="foot">
          <router-link to="/" tag="div"><div><i class="el-icon-eleme"></i> </div><span>外卖</span>
          </router-link>
        </div>
        <div class="foot">
          <router-link to="/Search" tag="div"><div><i class="el-icon-discover"></i></div><span>搜索</span>
          </router-link>
        </div>
        <div class="foot">
          <router-link to="/Order" tag="div"><div><i class="el-icon-notebook-1"></i></div><span>订单</span>
          </router-link>
        </div>
        <div class="foot">
          <router-link to="/mine" tag="div"> <div><i class="el-icon-user"></i></div><span>我的</span>
          </router-link>
        </div>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import indexHeader from '@/components/index-header.vue'
// import Swiper from '@/page/Swiper'
import axios from 'axios'
import Swiper from 'swiper'
import '../style/swiper.min.css'
import MescrollVue from 'mescroll.js/mescroll.vue'
// import IScroll from '@/js/iscroll-probe.js'
export default {
  components:{
    // Swiper,
    indexHeader,
     MescrollVue,
  },
  name: "Home",
  data() {
    return {
      current: 0,
      list: [],
      lunBo:[],
      listArr:[],
      mescroll: null, //mescroll实例对象
       mescroll: null, // mescroll实例对象
			      mescrollDown:{}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
			      mescrollUp: { // 上拉加载的配置.
			      callback: this.upCallback, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
			        //以下是一些常用的配置,当然不写也可以的.
				page: {
					num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
					size: 8 //每页数据条数,默认10
				},
				htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
				noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
			
			      },
      dataList:[], //列表数据
      foodAll:[]
    };
  },
    mounted(){
      axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F')
      .then(res => {
         if (res.data) {
            // console.log(res.data)
            let resLength = res.data.length;
            let resArr = res.data;
            let foodArr = [];
            for (var i = 0, j = 0; i < resLength; i += 8 , j++) {
               foodArr[j] = resArr.splice(0, 8)
            }
            this.lunBo = foodArr;
            // console.log(this.lunBo.length)
            for (var k = 0; k < this.lunBo.length; k++) {
               for (var z = 0; z < this.lunBo[k].length; z++) {
               }
            }
         }
      })
      .then(() => {
         new Swiper(".swiper-container", {
            // pagination:".swiper-pagination",
            pagination: {
               el: ".swiper-pagination"
            },
            loop: true,
            autoplay: true
         });
      })
   axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=')
      .then(res => {
         // console.log(res.data);
         this.foodAll = res.data;
      })
      axios.get(
         "http://elm.cangdu.org/shopping/restaurants?latitude=31.2708&longitude=121.50343&offset=0&limit=5&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=")
      .then(res => {
         this.foodAll = res.data; //获取城市id
         this.allLoaded = false;
        //  this.$refs.loadmore.onTopLoaded();
         // console.log(res.data)
      });
   window.addEventListener("scroll", this.scrollToTop);
},
		methods: {
      
        onChange(index) {
      this.current = index;
    },
   add(key){
    //  sessionStorage.details=JSON.stringify([this.arr[key]])
    // console.log('123')
     this.$router.push('Cart')
   },
			  // mescroll组件初始化的回调,可获取到mescroll对象
			mescrollInit (mescroll) {
			  this.mescroll = mescroll  // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
			},
			// 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
			upCallback (page, mescroll) {
			      // 联网请求
			  axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=', {
			    params: {
			      num: page.num, // 页码
			      size: page.size // 每页长度
			    }
			  }).then((response) => {
			    // 请求的列表数据
			    let arr = response.data
			    // 如果是第一页需手动置空列表
			    if (page.num === 1) this.dataList = []
			    // 把请求到的数据添加到列表
			    this.dataList = this.dataList.concat(arr)
			    // 数据渲染成功后,隐藏下拉刷新的状态
			    this.$nextTick(() => {
			      mescroll.endSuccess(arr.length)
			    })
			  }).catch((e) => {
			    // 联网失败的回调,隐藏下拉刷新和上拉加载的状态;
			    mescroll.endErr()
			  })
			}
		}
			
};

</script>

<style lang="scss" scoped>
#con {
   background-color: #f5f5f5;
   width: 100%;
   .swiper-container {
      background-color: #fff;
      width: 100%;
      height: 12rem;
      .swiper-slide {
         width: 100%;
         height: 100%;
         display: flex;
         flex-wrap: wrap;
         .slider {
            width: 25%;
            text-align: center;
            div:nth-of-type(1)>img{
               width: 3rem;
               height: 3rem;
            }
         }
      }
   }
}
.ggg{
  position: fixed;
  top: 0;
}
.vn-swipe{
  margin-top: 2rem;
}
.vn-swipe img {
  width: 60%;
  height: 30%;
}
.he-awiper {
  width: 100%;
  height: 30%;
  text-align: left;
  span {
    width: 65px;
    display: inline-block;
    font-size: 13px;
  }
}

.hea {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
.not1 {
  img {
    width: 1.5rem;
    height: 1.5rem;
  }
}
.not2 {
  color: white;
}
.not3 {
  img {
    width: 1.5rem;
    height: 1.5rem;
    color: white;
  }
}
.lbt {
  width: 100%;
  height: 10rem;
  display: flex;
  flex-wrap: wrap;
  margin-left: 20px;
  div {
    width: 25%;
    height: 5rem;
    padding: 0;
    margin: 0;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
  }
  img {
    width: 55px;
    height: 55px;
  }
  span {
    width: 100%;
    flex: 1;
    height: 30px;
    display: inline-block;
    font-size: 16px;
    // float: left;
  }
}
.mescroll {
  width: 100%;
	height: auto;
			  }
.house {
  width: 100%;
  height: auto;
  .testdiv {
    width: 100%;
    height: 22px;
    line-height: 22px;
  }
  .testimg {
    // float:left;
    display: inline;
    margin-left: 5px;
    img {
      width: 22px;
      height: 22px;
    }
  }
  .testtext {
    //  float:left;
    display: inline;
    //  margin-top:20;
    margin-left: 5px;
    color: gray;
  }
  .shop {
    width: 100%;
    //  height: 300px;
    margin-top: 20px;
  }
  .shopl1 {
    width: 100%;
    height: 90px;
    display: flex;
    margin-top: 20px;
    border-bottom: 1px solid gainsboro;
    .left {
      width: 20%;
      height: 90px;
      img {
        width: 80px;
        height: 90px;
      }
    }
    .right {
      width: 80%;
      height: 80px;
      margin-left: 20px;
      margin-top: -10px;
      p {
        width: 100%;
        height: 14px;
      }
    }
  }
}
#footers {
  position: fixed;
  width: 100%;
  height: 3rem;
  background: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 22.2rem;
  bottom:0;
}
#footer > div {
  width: 25%;
  margin-top: 0.1rem;
  text-align: center;
  i {
    font-size: 0.3rem;
    color: #787878;
  }
  span {
    font-size: 0.2rem;
  }
}
.foot > div:nth-of-type(1) {
  text-align: center;
}
.router-link-active {
  color: #309af6;
}
html,body{
  width:100%;
  height: 100%;
  margin: 0;
  padding: 0;
  touch-action:none;
}
</style>


