<template>
  <div>
    <div class="hea">
     <router-link tag='span' to='/Addreess'>
        <img src="@/images/110.png" alt=""><div class="not">新增地址</div>
    </router-link>
    </div>
    <div class="mgtop50 padlr10 bgfff padbot10" style="margin-top:50px;">
      <input class="cityinput" v-model="txt" type="text" placeholder="输入商务楼，学校，地址" />
      <div class="submit bgcol ih40" @click="onSearch()">确认</div>
    </div>

    <ul>
        <router-link :to="'/Pict?name='+item.name" class="li1" v-for="(item,key) in arr" :key="key">
          {{item.name}}<br />
          {{item.address}}
        </router-link>
    </ul>
  </div>
</template>

<script>
// import indexHeader from '@/components/index-header.vue'
import axios from 'axios'
export default {
  data() {
    return {
      inputval: "",
      list: "",
      txt:'',
      arr:[]
    };
  },
  components: {
    //注册组件
    // indexHeader
  },
  mounted: function() {
    //生命周期
  },
  computed: {
    //计算属性
  },
  methods: {
    //函数
    onSearch(){
      consonle.log(212)
      axios.get('http://elm.cangdu.org/v1/pois?type=nearby&keyword='+this.txt).then((res)=>{
          this.arr=res.data
          console.log(this.arr)
      })
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.hea{
    position: relative;
    top: 0;
    width:100%;
    height:3rem;
    // line-height: 3rem;
    color: white;
    img{
        width:1.5rem;
        height:1.5rem;
        margin-left: 1.6rem;
        margin-top: 0.8rem;
    }
    .not{
        display:inline-block;
        margin-left: 7.6rem;
        line-height: 3rem;
    }
}
.cityinput {
  width: 78%;
  height: 40px;
  // margin: 10px 0px;
  outline: 0px;
  padding: 0px 5px;
  float: left;
}
.submit {
  width: 3.9rem;
  text-align: center;
  color: white;
  border-radius: 3px;
  float: left;

}
.fs0-8 {
  font-size: 0.8rem !important;
}
.li1{
  width: 100%;
  height: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // bor
}
.main {
  border-top: 2px solid #e4e4e4;
}
.maintop {
  border-bottom: 2px solid #e4e4e4;
}
.clearall {
  text-align: center;
}
</style>


